<template>
        <!-- 모달창 -->
    <div class="black-bg" v-if="모달창==true">
    <div class="white-bg">
        <img :src="파크랜드[제목누른거].image" alt="img" style="width: 100%">
        <h4>{{파크랜드[제목누른거].title}}</h4>
        <p>상세 페이지 내용</p>
        <!-- <input type="text" @input="month=$event.target.value"> -->
        <input type="text" v-model.number="priceNumber">
        <p>{{priceNumber}}개를 선택함 : {{파크랜드[제목누른거].price * priceNumber}}원</p>
    <button @click="$emit('closeModal')">닫기</button>
        </div>
    </div>
</template>

<script>
export default {
    name : "modalPage",
    data() {
        return {
            priceNumber: 0,
        }
    },
        watch: {
            priceNumber(a){ // pirceNumber가 바뀔 때마다
                // console.log(kms)
                // 10개 이상 구매하면 11개 이상 예약 못합니다라는 문구 출력
                if(a >= 11){
                    alert("11개 이상 구매하지 못합니다.");
                }
                // 문자방지코드 문자입력 시 문자 입력하지마세요 출력
                // 이후에 값을 1로 되돌림
                if(isNaN(a)){
                    alert("문자x");
                    this.priceNumber = 1;
                }
            }
        },
    props : {
        파크랜드: Object, // 부모님이 가지고 있는 데이터 원형 => 데이터이름: 데이터형식
        제목누른거: Number,
        모달창: Boolean
    },
}
</script>

