<template>
        <div>
      <img :src=파크랜드.image alt="img" class="pg-img">
      <!-- emit : 커스텀 이벤트 실행-->
      <!-- <h4 @click="$emit('openModal', 파크랜드.id)">{{파크랜드.title}}</h4> -->
      <h4 @click="openModal()">{{파크랜드.title}}</h4>
      <p>{{파크랜드.price}}만원</p>
      <!-- <button @click="모달창=false">닫기</button> -->
    </div>
</template>

<script>
export default {
    props: {
        파크랜드: Object,
    },
    methods: {
        openModal(){
            // 뷰에서 함수, 변수 쓸 때 this키워드 활용
            // is not defined : this
            this.$emit('openModal', this.파크랜드.id);
        }
    },
}
</script>

